import { Component, OnInit, ChangeDetectionStrategy, Input } from "@angular/core";
import { sharedModuleEventHandlerType, SharedModuleEventHandlerService } from '@shared-event-handler';

@Component({
  selector: "tce-web-lesson-control",
  templateUrl: "./lesson-control.component.html",
  styleUrls: ["./lesson-control.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LessonControlComponent implements OnInit {
  @Input() public data: any;
  navigationType = [];


  public openOverflow: any = false;
  constructor(private sharedModuleEventHandlerService: SharedModuleEventHandlerService) { }

  public ngOnInit(): void {
    console.log(this.data)
  }
  public onNavAction(type: string) {
    this.sharedModuleEventHandlerService.broadcast(sharedModuleEventHandlerType.CONTROL_NAVIGATION, type);
  }

}


